<template>
  <div class="photovoltaic-monitor-container">

    <!-- 统计数据概览 -->
    <div class="stat-cards">
      <el-row :gutter="10">
        <el-col :span="24" :sm="12" :md="12" :lg="6" v-for="item in statData" :key="item.title">
          <el-card class="stat-card tech-card" shadow="hover">
            <div class="stat-content">
              <div class="stat-icon" :style="{ backgroundColor: item.color + '33', border: '1px solid ' + item.color }">
                <i :class="item.icon"></i>
              </div>
              <div class="stat-info">
                <div class="stat-value">{{ item.value }}<span class="unit">{{ item.unit }}</span></div>
                <div class="stat-title">{{ item.title }}</div>
                <div class="stat-change" :class="item.changeType">
                  {{ item.change }}
                </div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <!-- 图表区域 -->
    <div class="chart-section">
      <el-row :gutter="10">
        <el-col :span="24" :lg="12">
          <el-card shadow="hover" class="chart-card tech-card">
            <div slot="header" class="chart-header">
              <img src="@/assets/楼图标.png" alt="楼宇图标" class="card-icon-bottom" />
              <span class="card-text-bottom">发电功率实时曲线</span>
            </div>
            <div ref="powerChart" class="chart-container"></div>
          </el-card>
        </el-col>
        <el-col :span="24" :lg="12">
          <el-card shadow="hover" class="chart-card tech-card">
            <div slot="header" class="chart-header">
              <img src="@/assets/楼图标.png" alt="楼宇图标" class="card-icon-bottom" />
              <span class="card-text-bottom">日发电量统计</span>
            </div>
            <div ref="dailyChart" class="chart-container"></div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <!-- 设备状态和环境数据 -->
    <div class="data-section">
      <el-row :gutter="10" class="equal-height-row">
        <el-col :span="24" :lg="12">
          <el-card shadow="hover" class="data-card tech-card">
            <div slot="header" class="data-header">
              <img src="@/assets/楼图标.png" alt="楼宇图标" class="card-icon-bottom" />
              <span class="card-text-bottom">设备状态</span>
            </div>
            <div class="table-container">
              <el-table :data="equipmentData" style="width: 100%" border size="small">
                <el-table-column prop="name" label="设备名称" align="center"></el-table-column>
                <el-table-column prop="status" label="状态" align="center" width="80">
                  <template slot-scope="scope">
                    <el-tag :type="scope.row.status === '正常' ? 'success' : 'danger'" size="mini">
                      {{ scope.row.status }}
                    </el-tag>
                  </template>
                </el-table-column>
                <el-table-column prop="power" label="当前功率(kW)" align="center" width="120"></el-table-column>
                <el-table-column prop="efficiency" label="效率(%)" align="center" width="80"></el-table-column>
              </el-table>
            </div>
          </el-card>
        </el-col>
        <el-col :span="24" :lg="12">
          <el-card shadow="hover" class="data-card tech-card">
            <div slot="header" class="data-header">
              <img src="@/assets/楼图标.png" alt="楼宇图标" class="card-icon-bottom" />
              <span class="card-text-bottom">环境数据</span>
            </div>
            <div class="environment-data">
              <div class="env-items-container">
                <div class="env-row" v-for="(row, rowIndex) in environmentRows" :key="rowIndex">
                  <div class="env-item" v-for="(item, index) in row" :key="index">
                    <div class="env-info">
                      <div class="env-name">{{ item.name }}</div>
                      <div class="env-value">{{ item.value }}{{ item.unit }}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: 'GuangfuFadian',
  data() {
    return {
      currentTime: new Date().toLocaleString(),
      statData: [
        {
          title: '当前功率',
          value: '1245.6',
          unit: 'kW',
          icon: 'el-icon-lightning',
          color: '#409EFF',
          change: '↑ 2.3% 同比',
          changeType: 'increase'
        },
        {
          title: '日发电量',
          value: '8675.2',
          unit: 'kWh',
          icon: 'el-icon-sunrise',
          color: '#67C23A',
          change: '↑ 5.2% 同比',
          changeType: 'increase'
        },
        {
          title: '月发电量',
          value: '245678',
          unit: 'kWh',
          icon: 'el-icon-date',
          color: '#E6A23C',
          change: '↑ 3.1% 同比',
          changeType: 'increase'
        },
        {
          title: '总发电量',
          value: '1245678',
          unit: 'kWh',
          icon: 'el-icon-data-line',
          color: '#F56C6C',
          change: '↑ 1.2% 同比',
          changeType: 'increase'
        }
      ],
      equipmentData: [
        {
          name: '光伏组串1',
          status: '正常',
          power: '245.6',
          efficiency: '92.5'
        },
        {
          name: '光伏组串2',
          status: '正常',
          power: '312.4',
          efficiency: '94.2'
        },
        {
          name: '光伏组串3',
          status: '正常',
          power: '189.7',
          efficiency: '89.8'
        },
        {
          name: '光伏组串4',
          status: '正常',
          power: '278.9',
          efficiency: '91.6'
        },
        {
          name: '光伏组串5',
          status: '异常',
          power: '0',
          efficiency: '0'
        },
        {
          name: '光伏组串5',
          status: '异常',
          power: '0',
          efficiency: '0'
        },
        {
          name: '光伏组串5',
          status: '异常',
          power: '0',
          efficiency: '0'
        }
      ],
      environmentData: [
        {
          name: '辐照度',
          value: '856',
          unit: 'W/㎡',
          icon: 'el-icon-sunny',
          color: '#FFD700'
        },
        {
          name: '温度',
          value: '28.5',
          unit: '℃',
          icon: 'el-icon-sunny',
          color: '#FF6347'
        },
        {
          name: '风速',
          value: '3.2',
          unit: 'm/s',
          icon: 'el-icon-wind',
          color: '#87CEEB'
        },
        {
          name: '湿度',
          value: '45',
          unit: '%',
          icon: 'el-icon-water',
          color: '#4682B4'
        },
        {
          name: '气压',
          value: '101.3',
          unit: 'kPa',
          icon: 'el-icon-guide',
          color: '#9370DB'
        },
        {
          name: '风向',
          value: '西南',
          unit: '',
          icon: 'el-icon-location',
          color: '#32CD32'
        }
      ],
      powerChart: null,
      dailyChart: null
    }
  },
  computed: {
    environmentRows() {
      // 将环境数据分为两行，每行三个元素
      const rows = [];
      for (let i = 0; i < this.environmentData.length; i += 3) {
        rows.push(this.environmentData.slice(i, i + 3));
      }
      return rows;
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initCharts();
      // 监听窗口大小变化，重新调整图表大小
      window.addEventListener('resize', this.resizeCharts);
      // 模拟定时刷新数据
      this.timer = setInterval(() => {
        this.updateData();
      }, 30000); // 每30秒刷新一次
    });
  },
  beforeDestroy() {
    if (this.powerChart) {
      this.powerChart.dispose();
    }
    if (this.dailyChart) {
      this.dailyChart.dispose();
    }
    window.removeEventListener('resize', this.resizeCharts);
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  methods: {
    initCharts() {
      // 发电功率实时曲线
      this.powerChart = echarts.init(this.$refs.powerChart);
      const powerOption = {
        tooltip: {
          trigger: 'axis',
          textStyle: {
            color: '#c0d3e2'
          },
          backgroundColor: 'rgba(13, 33, 63, 0.9)'
        },
        legend: {
          data: ['发电功率'],
          top: '5%',
          right: '5%',
          textStyle: {
            color: '#4fc3f7'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['08:00', '09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00'],
          axisLine: {
            lineStyle: {
              color: '#4fc3f7'
            }
          },
          axisLabel: {
            color: '#93c5fd'
          },
          axisTick: {
            lineStyle: {
              color: '#4fc3f7'
            }
          }
        },
        yAxis: {
          type: 'value',
          name: '功率(kW)',
          nameTextStyle: {
            color: '#93c5fd'
          },
          axisLine: {
            lineStyle: {
              color: '#4fc3f7'
            }
          },
          axisLabel: {
            color: '#93c5fd'
          },
          axisTick: {
            lineStyle: {
              color: '#4fc3f7'
            }
          },
          splitLine: {
            lineStyle: {
              color: 'rgba(79, 195, 247, 0.2)'
            }
          }
        },
        series: [
          {
            name: '发电功率',
            type: 'line',
            data: [120, 350, 680, 920, 1150, 1245, 1120, 890, 620],
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: 'rgba(64, 158, 255, 0.8)'
                },
                {
                  offset: 1,
                  color: 'rgba(64, 158, 255, 0.1)'
                }
              ])
            },
            lineStyle: {
              color: '#409EFF'
            },
            itemStyle: {
              color: '#409EFF',
              borderColor: '#4fc3f7',
              borderWidth: 2
            }
          }
        ]
      };
      this.powerChart.setOption(powerOption);

      // 日发电量统计
      this.dailyChart = echarts.init(this.$refs.dailyChart);
      const dailyOption = {
        tooltip: {
          trigger: 'axis',
          textStyle: {
            color: '#c0d3e2'
          },
          backgroundColor: 'rgba(13, 33, 63, 0.9)'
        },
        legend: {
          data: ['发电量'],
          top: '5%',
          right: '5%',
          textStyle: {
            color: '#4fc3f7'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
          axisLine: {
            lineStyle: {
              color: '#4fc3f7'
            }
          },
          axisLabel: {
            color: '#93c5fd'
          },
          axisTick: {
            lineStyle: {
              color: '#4fc3f7'
            }
          }
        },
        yAxis: {
          type: 'value',
          name: '发电量(kWh)',
          nameTextStyle: {
            color: '#93c5fd'
          },
          axisLine: {
            lineStyle: {
              color: '#4fc3f7'
            }
          },
          axisLabel: {
            color: '#93c5fd'
          },
          axisTick: {
            lineStyle: {
              color: '#4fc3f7'
            }
          },
          splitLine: {
            lineStyle: {
              color: 'rgba(79, 195, 247, 0.2)'
            }
          }
        },
        series: [
          {
            name: '发电量',
            type: 'bar',
            barWidth: '60%',
            data: [7850, 8230, 6540, 8675, 9210, 7560, 6890],
            itemStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: '#67C23A' },
                { offset: 1, color: '#E6A23C' }
              ])
            }
          }
        ]
      };
      this.dailyChart.setOption(dailyOption);
    },
    resizeCharts() {
      if (this.powerChart) {
        this.powerChart.resize({
          animation: {
            duration: 300
          }
        });
      }
      if (this.dailyChart) {
        this.dailyChart.resize({
          animation: {
            duration: 300
          }
        });
      }
    },
    refreshData() {
      this.updateData();
      this.$message.success('数据已刷新');
    },
    updateData() {
      // 更新时间
      this.currentTime = new Date().toLocaleString();
      
      // 模拟更新统计数据
      this.statData[0].value = (Math.random() * 1000 + 500).toFixed(1);
      this.statData[1].value = (Math.random() * 1000 + 8000).toFixed(1);
      
      // 模拟更新设备状态
      const randomIndex = Math.floor(Math.random() * this.equipmentData.length);
      if (Math.random() > 0.9) { // 10%概率出现异常
        this.equipmentData[randomIndex].status = '异常';
        this.equipmentData[randomIndex].power = '0';
        this.equipmentData[randomIndex].efficiency = '0';
      } else {
        this.equipmentData[randomIndex].status = '正常';
        this.equipmentData[randomIndex].power = (Math.random() * 400).toFixed(1);
        this.equipmentData[randomIndex].efficiency = (Math.random() * 10 + 85).toFixed(1);
      }
      
      // 模拟更新环境数据
      this.environmentData[0].value = Math.floor(Math.random() * 300 + 700);
      this.environmentData[1].value = (Math.random() * 10 + 25).toFixed(1);
      this.environmentData[2].value = (Math.random() * 5).toFixed(1);
      this.environmentData[3].value = Math.floor(Math.random() * 30 + 30);
    }
  }
}
</script>

<style scoped>
.photovoltaic-monitor-container {
  height: calc(100vh - 20px);
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  overflow: auto;
  display: flex;
  flex-direction: column;
  background: url('../../assets/1.gif') center center no-repeat;
  background-size: cover;
  color: #e0e0ff;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  position: relative;
}

.photovoltaic-monitor-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 25, 41, 0.85);
  z-index: -1;
}

.stat-cards {
  margin-bottom: 10px;
  flex-shrink: 0;
}

.stat-card {
  border: none;
  margin-bottom: 10px;
  background: linear-gradient(145deg, rgba(12, 33, 58, 0.9), rgba(10, 30, 55, 0.9));
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(26, 54, 93, 0.7);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  height: 100px;
  display: flex;
  align-items: center;
  z-index: 10;
}

.stat-card::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    to bottom right,
    rgba(26, 107, 194, 0.1),
    transparent,
    transparent,
    transparent
  );
  transform: rotate(30deg);
  transition: all 0.6s ease;
  z-index: -1;
}

.stat-card:hover::before {
  transform: rotate(30deg) translate(20%, 20%);
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(77, 171, 247, 0.8);
  z-index: 20;
}

.stat-content {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.stat-icon {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
  flex-shrink: 0;
  background: rgba(64, 158, 255, 0.2);
  border: 1px solid #409EFF;
}

.stat-icon i {
  font-size: 20px;
  color: #409EFF;
  text-shadow: 0 0 5px rgba(64, 158, 255, 0.7);
}

.stat-info {
  flex: 1;
  text-align: left;
  overflow: hidden;
}

.stat-value {
  font-size: 18px;
  font-weight: bold;
  color: #a3cfff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-shadow: 0 0 8px rgba(77, 171, 247, 0.7);
  margin-bottom: 3px;
}

.unit {
  font-size: 12px;
  margin-left: 2px;
  color: #81d4fa;
}

.stat-title {
  font-size: 12px;
  color: #79b1ff;
  margin: 2px 0;
}

.stat-change {
  font-size: 11px;
  font-weight: 500;
}

.stat-change.decrease {
  color: #69db7c;
  text-shadow: 0 0 6px rgba(105, 219, 124, 0.7);
}

.stat-change.increase {
  color: #ff6b6b;
  text-shadow: 0 0 6px rgba(255, 107, 107, 0.7);
}

.chart-section {
  margin-bottom: 10px;
  flex-shrink: 0;
}

.data-section {
  flex: 1;
  margin-bottom: 10px;
  overflow: hidden;
  min-height: 0;
  padding: 5px 0;
}

.equal-height-row {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.equal-height-row > [class*="el-col-"] {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.chart-card,
.data-card {
  border: none;
  height: 100%;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  background: linear-gradient(145deg, rgba(12, 33, 58, 0.9), rgba(10, 30, 55, 0.9));
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(26, 54, 93, 0.7);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  padding: 5px 0;
}

.chart-card::before,
.data-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    transparent,
    rgba(77, 171, 247, 0.08),
    transparent
  );
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}

.chart-card:hover::before,
.data-card:hover::before {
  transform: translateX(100%);
}

.chart-card:hover,
.data-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(77, 171, 247, 0.8);
  z-index: 100;
}

.chart-header,
.data-header {
  font-weight: bold;
  color: #a3cfff;
  font-size: 16px;
  background: linear-gradient(to right, rgba(10, 30, 55, 0.9), rgba(12, 37, 69, 0.9));
  padding: 10px !important;
  border-bottom: 1px solid rgba(26, 54, 93, 0.7);
  position: relative;
  display: flex;
  align-items: center;
}

.chart-header::after,
.data-header::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(to right, transparent, rgba(77, 171, 247, 0.8), transparent);
}

.card-icon-bottom {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  vertical-align: middle;
}

.card-text-bottom {
  color: #a3cfff;
  font-size: 16px;
  font-weight: bold;
  text-shadow: 0 0 8px rgba(79, 195, 247, 0.7);
  letter-spacing: 1px;
}

.chart-container {
  flex: 1;
  min-height: 200px;
  width: 100%;
  background: rgba(10, 20, 35, 0.5);
}

.table-container {
  height: 250px;
  overflow-y: auto;
  background: rgba(10, 20, 35, 0.3);
  /* 隐藏滚动条但保持滚动功能 */
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
}

.table-container::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}

/* 科技风格的表格样式 */
.table-container :deep(.el-table) {
  background: transparent;
  border: none;
}

.table-container :deep(.el-table__header-wrapper) {
  background: linear-gradient(to right, rgba(10, 30, 55, 0.9), rgba(12, 37, 69, 0.9));
}

.table-container :deep(.el-table th) {
  background-color: transparent;
  color: #a3cfff;
  font-weight: bold;
  border: none;
}

.table-container :deep(.el-table td) {
  background-color: rgba(10, 30, 55, 0.5);
  color: #e0e0ff;
  border: none;
}

.table-container :deep(.el-table__body-wrapper) {
  background: transparent;
}

.table-container :deep(.el-table tr) {
  background-color: rgba(10, 30, 55, 0.3);
}

.table-container :deep(.el-table tr:hover) {
  background-color: rgba(77, 171, 247, 0.2) !important;
}

.table-container :deep(.el-table__body-wrapper tr:hover > td) {
  background-color: rgba(77, 171, 247, 0.2) !important;
}

.table-container :deep(.el-table::before) {
  background-color: rgba(26, 54, 93, 0.7);
}

.table-container :deep(.el-table--border::after) {
  background-color: rgba(26, 54, 93, 0.7);
}

.table-container :deep(.el-table--border th) {
  border-right: 1px solid rgba(26, 54, 93, 0.7);
}

.table-container :deep(.el-table--border td) {
  border-right: 1px solid rgba(26, 54, 93, 0.7);
  border-bottom: 1px solid rgba(26, 54, 93, 0.7);
}

.environment-data {
  padding: 10px;
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.env-items-container {
  display: flex;
  flex-direction: column;
  height: 100%;
